.landing {
	text-align: center;
	height: calc(100vh - 70px);
	align-items: center;
	justify-content: center;
	padding-top: 3rem;
}

.landing-img {
	width: 12vw;
	height: auto;
	margin-right: 10px;
	margin-left: 10px;
	transform: translateY(-10px);
}

.subtitle {
	font-size: 1.5rem;
}
.bounce {
    -webkit-animation: bounce 2s infinite;
    animation: bounce 2s infinite;
  }
img.down-arrow {
	height: 1rem;
	cursor: pointer;
}
@-webkit-keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
      transform: translateY(0);
    }
    40% {
      transform: translateY(-10px);
    }
    60% {
      transform: translateY(-10px);
    }
  }
@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
      transform: translateY(0);
    }
    40% {
      transform: translateY(-10px);
    }
    60% {
      transform: translateY(-10px);
    }
  }
